import React from 'react';
import { Button } from '../Button';

export class Counter extends React.Component {
  state = {
    count: 0,
  }

  addOne = () => {
    this.setState(prevState => ({
      count: prevState.count + 1,
    }));
  };

  add100 = () => {
    this.setState(prevState => ({
      count: prevState.count + 100,
    }));
  };

  increase = () => {
    const { count } = this.state;

    if (count % 5 === 0) {
      this.add100();
    }

    this.addOne();
  };

  render() {
    const { count } = this.state;

    return (
      <div className="box mt-4">
        <h1 className="title has-text-primary-dark">
          {`Count: ${count}`}
        </h1>
        <Button text="Add 1" onClick={this.addOne} color="is-primary" />
        <Button text="Add 100" onClick={this.add100} color="is-warning" />
        <Button text="Increase" onClick={this.increase} color="is-danger" />
      </div>
    );
  }
}
